// @mui
import { Box, Card, Container, Typography } from '@mui/material';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import Markdown from '../../components/Markdown';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import { HOST_API, } from '../../config';
// sections
// hooks
import useAuth from '../../hooks/useAuth';
// ----------------------------------------------------------------------
export default function DocsHome() {
  const { themeStretch } = useSettings();

  const {setting, user} = useAuth();

  return (
    <Page title="Docs">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="Introduction"
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.root },
            { name: 'Home' },
          ]}
        />

       
          <Card>

            <Box sx={{ p: { xs: 3, md: 5 } }}>
              <Typography  sx={{ mb: 5 }}>
                Introduction
                           </Typography>

              <Markdown children={`Welcome to the ${setting?.general?.app_name} ! You can use our API to access ${setting?.general?.app_name} API endpoints, which can be use to purchase any of our services, and breeds in our database.

We have language bindings in PHP, Ruby, Python, react js and JavaScript! You can use any programming language of your choice to connect to our endpoints.

This  ${setting?.general?.app_name} API documentation page was created to assits you through the integration proccess.

Thanks.`} />

        
                <Markdown children={`<hr> <h3>Authentication</h3><br/> <p>How to Generate (TOKEN)</p>
                <p><br></p><p>
                The Basic Authentication Is Use To Generate (TOKEN) <br>
                It should be passed as a concatenated string like this
               <b> username:password </b> <br>
                Please use the following details for authentication <br>
               <b> Username: Your ${setting?.general?.app_name} Username </b> <br>
                 <b> Password: Your ${setting?.general?.app_name} Password </b><br>
                convert it to <b> base64(username:password) </b> use it to gerenerate  token
                </p><p><br></p><p>
                <hr> <h3>Enpoint URL</h3><br/> <blockquote>${HOST_API}/api/user</blockquote>
                <hr> <h3>Code Sample</h3><br/>
                <pre class="ql-syntax" spellcheck="false">
                 $ch = curl_init();
                curl_setopt($ch, CURLOPT_URL, "${HOST_API}/api/user");
                curl_setopt($ch, CURLOPT_POST, 1);
                curl_setopt($ch, CURLOPT_RETURNTRANSFER, 1);
                curl_setopt(
                   $ch, CURLOPT_HTTPHEADER, [
                        "Authorization: Basic ".base64_encode('username:password'),
                    ]
                );
                $json = curl_exec($ch);
                curl_close($ch); </pre><br>
                <hr> <h3>Expected Response</h3><br/>
                <pre class="ql-syntax" spellcheck="false">
                {
                    "status": "success",
                    "AccessToken": "${user?.apikey}",
                    "balance": "${user?.bal}",
                    "username": "${user?.username}"
                }
                </pre>
                
                `} />
           
            

             

           

           

         
            </Box>
          </Card>
      </Container>
    </Page>
  );
}
